import Main from '@/views/Main.vue';

// Main
export const loginRouter = {
    path: '/login',
    name: 'login',
    meta: {
        title: 'Login - '
    },
    component: () => import('@/views/login.vue')
};

export const registerRouter = {
    path: '/register',
    name: 'register',
    meta: {
        title: 'register - '
    },
    component: () => import('@/views/register.vue')
};

export const page404 = {
    path: '/*',
    name: 'error-404',
    meta: {
        title: '404-'
    },
    component: () => import('@/views/error-page/404.vue')
};

export const page403 = {
    path: '/403',
    meta: {
        title: '403-'
    },
    name: 'error-403',
    component: () => import('@//views/error-page/403.vue')
};

export const page500 = {
    path: '/500',
    meta: {
        title: '500-'
    },
    name: 'error-500',
    component: () => import('@/views/error-page/500.vue')
};

export const locking = {
    path: '/locking',
    name: 'locking',
    component: () => import('@/views/main-components/lockscreen/components/locking-page.vue')
};

// MainotherRouter
export const otherRouter = {
    path: '/',
    name: 'otherRouter',
    redirect: '/home',
    component: Main,
    children: [
        { path: 'home', title: { i18n: 'home' }, name: 'home_index', component: () => import('@/views/home/home.vue') },
        {
            path: 'ownspace',
            title: '',
            name: 'ownspace_index',
            component: () => import('@/views/own-space/own-space.vue')
        },
        {
            path: 'message',
            title: '',
            name: 'message_index',
            component: () => import('@/views/message/message.vue')
        }
    ]
};

// MainappRouter
export const appRouter = [
    {
        path: '/access',
        icon: 'key',
        name: 'access',
        title: '',
        component: Main,
        children: [
            {
                path: 'index',
                title: '',
                name: 'access_index',
                component: () => import('@/views/access/access.vue')
            }
        ]
    },
    {
        path: '/access-test',
        icon: 'lock-combination',
        title: '',
        name: 'accesstest',
        access: 0,
        component: Main,
        children: [
            {
                path: 'index',
                title: '',
                name: 'accesstest_index',
                access: 0,
                component: () => import('@/views/access/access-test.vue')
            }
        ]
    },
    {
        path: '/blog',
        icon: 'social-buffer',
        name: 'component',
        title: '',
        component: Main,
        children: [
            {
                path: 'category-list',
                icon: 'ios-pricetags-outline',
                name: 'category-list',
                title: '',
                component: () => import('@/views/blog/category-list/category-list.vue')
            },
            {
                path: 'article-publish',
                icon: 'compose',
                name: 'article-publish',
                title: '',
                component: () => import('@/views/blog/article-publish/article-publish.vue')
            },
            {
                path: 'article-list',
                icon: 'ios-list-outline',
                name: 'article-list',
                title: '',
                component: () => import('@/views/blog/article-list/article-list.vue')
            }
        ]
    },
    {
        path: '/image',
        icon: 'android-upload',
        name: 'image',
        title: '',
        component: Main,
        children: [
            {
                path: 'upload',
                title: '',
                name: 'upload',
                component: () => import('@/views/image-manage/upload/upload.vue')
            }
        ]
    }
];

// routers
export const routers = [loginRouter, registerRouter, otherRouter, locking, ...appRouter, page500, page403, page404];



